.eggShell {
  background-color: #000000;
  color: #ffffff;
}

.innerShell h1:first-child {
  font-size: 4rem;
}

.innerShell h1:nth-child(2) {
  font-size: 3rem;
}

.innerShell h1 {
  font-family: "Advent Pro", sans-serif;
  text-align: center;
}

.innerShell h2 {
  font-family: "Staatliches", cursive;
  text-align: center;
  font-size: 3rem;
}

.egg {
  max-height: 560px;
  max-width: 330px;
  height: 560px;
  width: 330px;
  border-radius: 500px/800px;
  display: block;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
  background: linear-gradient(45deg, #f17c58, #e94584, #24aadb, #27dbb1, #ffdc18, #ff3706);
  background-size: 600% 100%;
  -webkit-animation: gradient 16s linear infinite;
          animation: gradient 16s linear infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  overflow-x: hidden;
  text-shadow: 3px 3px 10px #f1e91c;
  filter: drop-shadow(10px 10px 300px #f1e91c);
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}

.piedPiper {
  display: block;
  position: absolute;
  top: 55px;
  height: 290px;
  width: auto;
  filter: drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.4));
}

.yolk {
  margin: 0;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-bottom: 2rem;
  padding-top: 200px;
  font-size: 1.25rem;
  font-family: "Raleway", sans-serif;
  text-shadow: 3px 3px 10px #000000;
  text-align: center;
}

.eggHeader {
  padding-top: 50px;
  margin: 0;
  filter: drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.4));
}