.homeWrapper {
  color: #ffffff;
  overflow: hidden;
  list-style-type: square;
  overflow-x: hidden;
  width: 100%;
}

/* For extremely small screen devices (500px and below) */
@media screen and (max-width: 500px) {
  .intro {
    padding: 2rem;
    display: block;
    margin: auto;
  }

  #hiRoz {
    font-family: "Audiowide", cursive;
    font-size: 3rem;
    text-align: center;
    padding-bottom: 1rem;
    margin: 0;
  }

  .logo,
.leftBracket,
.rightBracket {
    opacity: 0;
    height: 0;
  }

  #drop {
    font-size: 3rem;
    font-family: "Audiowide", cursive;
    color: #ffffff;
  }

  .subtitle {
    font-family: "Raleway", sans-serif;
    font-size: 2rem;
  }

  #netrunner,
#apollo {
    max-width: 70vw;
    height: auto;
    border-radius: 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  #apollo {
    margin-bottom: 2rem;
  }

  .introSpan > * {
    text-align: center;
  }

  .hobbies {
    font-family: "Advent Pro", sans-serif;
    font-size: 1.5rem;
  }

  .stackWrap {
    justify-items: center;
  }

  .stack {
    display: inline-flex;
    font-size: 3rem;
    font-family: "Audiowide", cursive;
    text-shadow: 0.05em 0 0 rgba(255, 0, 0, 0.75), -0.025em -0.05em 0 rgba(0, 255, 0, 0.75), 0.025em 0.05em 0 rgba(0, 0, 255, 0.75);
    -webkit-animation: glitch 552ms infinite;
            animation: glitch 552ms infinite;
  }

  .stack span {
    position: absolute;
    z-index: -1;
  }

  .stack span:first-child {
    -webkit-animation: glitch 731ms infinite;
            animation: glitch 731ms infinite;
  }

  .stack span:last-child {
    -webkit-animation: glitch 373s infinite;
            animation: glitch 373s infinite;
  }

  .stackList {
    align-content: space-between;
    font-size: 1.5rem;
    font-family: "Advent Pro", sans-serif;
  }

  .sl:hover {
    text-shadow: 0.05em 0 0 rgba(255, 0, 0, 0.75), -0.025em -0.05em 0 rgba(0, 255, 0, 0.75), 0.025em 0.05em 0 rgba(0, 0, 255, 0.75);
    -webkit-animation: glitch 450ms infinite;
            animation: glitch 450ms infinite;
  }

  .goals h1 {
    font-family: "Audiowide", cursive;
    font-size: 3rem;
    text-align: center;
  }

  .goals h3 {
    font-family: "Advent Pro", sans-serif;
    font-size: 2rem;
  }

  .goals ul {
    list-style-type: square;
    font-family: "Raleway", sans-serif;
  }

  .goals p {
    font-family: "Advent Pro", sans-serif;
    font-size: 1.5rem;
  }
}
